.App {
  min-height: 100vh;
  position: relative;
}

/* Custom Toast Styles - X Button and Apple-style Stacking */

/* React-hot-toast container and toast elements */
[data-testid="toast-container"] {
  z-index: 9999 !important;
}

/* React-hot-toast specific classes */
[data-testid="toast"] {
  position: relative !important;
  padding-right: 28px !important;
  transition: all 0.3s ease !important;
}

/* Close button for react-hot-toast - Multiple selectors for better targeting */
[aria-label="Close toast"],
[data-testid="toast"] button[aria-label="Close toast"],
[data-testid="toast-container"] button[aria-label="Close toast"],
button[aria-label="Close toast"],
.Toastify__close-button,
.Toastify__close-button > button {
  position: absolute !important;
  right: 12px !important;
  top: 12px !important;
  background: #9333ea !important;
  border: none !important;
  border-radius: 6px !important;
  color: white !important;
  width: 24px !important;
  height: 24px !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  font-weight: bold !important;
  transition: all 0.3s ease !important;
  cursor: pointer !important;
  opacity: 1 !important;
  visibility: visible !important;
  font-size: 16px !important;
  line-height: 1 !important;
  z-index: 1000 !important;
  min-width: 24px !important;
  min-height: 24px !important;
}

/* Force the X content */
[aria-label="Close toast"]:before,
[data-testid="toast"] button[aria-label="Close toast"]:before,
[data-testid="toast-container"] button[aria-label="Close toast"]:before,
button[aria-label="Close toast"]:before,
.Toastify__close-button:before,
.Toastify__close-button > button:before {
  content: 'Ã—' !important;
  font-size: 18px !important;
  font-weight: 300 !important;
  color: white !important;
}

/* Force the button to show */
[aria-label="Close toast"]:hover,
[data-testid="toast"] button[aria-label="Close toast"]:hover,
[data-testid="toast-container"] button[aria-label="Close toast"]:hover,
button[aria-label="Close toast"]:hover,
.Toastify__close-button:hover,
.Toastify__close-button > button:hover {
  background: #7c3aed !important;
  transform: scale(1.1) !important;
}

/* Additional targeting for any close button */
button[aria-label*="close"],
button[aria-label*="Close"],
button[title*="close"],
button[title*="Close"] {
  position: absolute !important;
  right: 12px !important;
  top: 12px !important;
  background: #9333ea !important;
  border: none !important;
  border-radius: 6px !important;
  color: white !important;
  width: 24px !important;
  height: 24px !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  font-weight: bold !important;
  transition: all 0.3s ease !important;
  cursor: pointer !important;
  opacity: 1 !important;
  visibility: visible !important;
  font-size: 16px !important;
  line-height: 1 !important;
  z-index: 1000 !important;
}

/* Apple-style visual stacking for react-hot-toast */
[data-testid="toast-container"] > div {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;
  transform-origin: center top !important;
}

/* Stack notifications on top of each other like Apple */
[data-testid="toast-container"] > div:nth-child(1) {
  transform: translateY(0) scale(1) !important;
  z-index: 100 !important;
}

[data-testid="toast-container"] > div:nth-child(2) {
  transform: translateY(-8px) scale(0.98) !important;
  z-index: 99 !important;
  opacity: 0.95 !important;
}

[data-testid="toast-container"] > div:nth-child(3) {
  transform: translateY(-16px) scale(0.96) !important;
  z-index: 98 !important;
  opacity: 0.9 !important;
}

[data-testid="toast-container"] > div:nth-child(4) {
  transform: translateY(-24px) scale(0.94) !important;
  z-index: 97 !important;
  opacity: 0.85 !important;
}

[data-testid="toast-container"] > div:nth-child(5) {
  transform: translateY(-32px) scale(0.92) !important;
  z-index: 96 !important;
  opacity: 0.8 !important;
}

[data-testid="toast-container"] > div:nth-child(n+6) {
  transform: translateY(-40px) scale(0.9) !important;
  z-index: 95 !important;
  opacity: 0.75 !important;
}

/* Hover effect - expand the stack */
[data-testid="toast-container"]:hover > div {
  transform: translateY(0) scale(1) !important;
  opacity: 1 !important;
}

[data-testid="toast-container"]:hover > div:nth-child(1) {
  z-index: 100 !important;
}

[data-testid="toast-container"]:hover > div:nth-child(2) {
  transform: translateY(60px) scale(1) !important;
  z-index: 99 !important;
}

[data-testid="toast-container"]:hover > div:nth-child(3) {
  transform: translateY(120px) scale(1) !important;
  z-index: 98 !important;
}

[data-testid="toast-container"]:hover > div:nth-child(4) {
  transform: translateY(180px) scale(1) !important;
  z-index: 97 !important;
}

[data-testid="toast-container"]:hover > div:nth-child(5) {
  transform: translateY(240px) scale(1) !important;
  z-index: 96 !important;
}

[data-testid="toast-container"]:hover > div:nth-child(n+6) {
  transform: translateY(300px) scale(1) !important;
  z-index: 95 !important;
}

/* Purple mesh gradient background */
.App::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(139, 92, 246, 0.04) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(107, 70, 255, 0.04) 0%, transparent 50%),
    radial-gradient(circle at 40% 20%, rgba(163, 137, 255, 0.03) 0%, transparent 50%);
  pointer-events: none;
  z-index: 0;
}

.content-wrapper {
  position: relative;
  z-index: 1;
}

/* Animations */
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(107, 70, 255, 0.3);
  }
  50% {
    box-shadow: 0 0 40px rgba(139, 92, 246, 0.5);
  }
}

.animate-float {
  animation: float 6s ease-in-out infinite;
}

.animate-pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}

/* Smooth page transitions */
.page-transition-enter {
  opacity: 0;
  transform: translateY(20px);
}

.page-transition-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 300ms, transform 300ms;
}

.page-transition-exit {
  opacity: 1;
}

.page-transition-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}
